.dialog-window {
  background-color: #ffffff;
  width: 445px;
  border-radius: 5px;
  box-shadow: -1px 0px 13px 1px rgba(41, 41, 42, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 35px 0px;
  @media(max-width: 540px) {
    width: 100%
  }
}

.dialog-window__wrapper {
  position: absolute;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(140, 152, 165, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  @media(max-width: 540px) {
    background-color: rgba(89, 101, 114, 0.9)
  }
}

.dialow-window__headline {
  font-size: 24px;
  text-align: center;
}